@import url('https://fonts.googleapis.com/css2?family=Inter:wght@700&family=Karla:wght@700&display=swap');

* {
    box-sizing: border-box;
}

body {
    margin: 0;
    background-color: #0B2434;
    padding: 20px;
    font-family: 'Karla', sans-serif;
    overflow: hidden;
}

main{
    
    background-color: #f5f5f5;
    height:500px;
    max-width: 100%;
    border-radius: 5px;
    padding: 20px;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    align-items: center;
}

.title {
    font-size: 40px;
    margin: 0;
}

.instructions {
    font-family: 'Inter', sans-serif;
    font-weight: 400;
    margin-top: 0;
    text-align: center;
}

.dice-container{
    display: grid;
    grid-template: auto auto / repeat(5, 1fr);  
    gap: 40px;
    margin-bottom: 40px;
}

.die-face{
    height: 50px;
    width: 50px;
    box-shadow: 0px 2px 2px rgba(0, 0, 0, 0.15);
    border-radius: 10px;
    display: flex;
    justify-content: center;
    align-items: center;
    cursor: pointer;
}

.die-num{
    font-size: 2rem;
}

.roll-dice{
    height: 50px;
    width: 150px;
    color: #FFFFFF;
    background-color: #5035FF;
    border:none;
    border-radius: 6px;
    font-size: 1.2rem;
    font-family: 'Karla', sans-serif;
    cursor: pointer;
}

.roll-dice:focus{
    outline: none;
}

.roll-dice:active{
   box-shadow : inset 5px 5px 10px -3px rgba(0, 0, 0, 0.7);
}

.score{
    margin-bottom: 0;
}
.best-score{
    margin-top: 0;
}

@media (max-width : 469px) {
    .dice-container{
        gap : 20px;
    }
}

@media (max-width : 376px) {
    .dice-container{
        gap : 15px;
    }
}